.login-page {
  width: 100vw;
  height: 100vh;
  background-image: url(/static/media/loginBg.57281334.jpg);
  background-size: cover;
  position: relative;
  background-position: 50% center;
}
.login-page .ant-input-group-addon .anticon {
  display: block;
}
.login-page form .ant-input-group,
.login-page form .ant-input-group-wrappe {
  display: inline-table;
}
.login-page .login-content {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -60%);
          transform: translate(-50%, -60%);
  width: 300px;
}
.login-page .login-content.login-mobile {
  left: 0;
  right: 0;
  margin: auto;
}
.login-page .login-content .ivu-card {
  font-size: 14px;
  position: relative;
  background: #ffffff;
  border-radius: 4px;
  transition: all 0.2s ease-in-out;
}
.login-page .login-content .ivu-card-head {
  line-height: 1;
  border-bottom: 1px solid #e9eaec;
  padding: 14px 16px;
}
.login-page .login-content .ivu-card-head-inner,
.login-page .login-content .ivu-card-head p {
  display: inline-block;
  width: 100%;
  height: 20px;
  line-height: 20px;
  font-size: 14px;
  color: #1c2438;
  font-weight: 700;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.login-page .ivu-card-body {
  padding: 6px 16px 16px;
}
.login-page .ivu-card-body .tips {
  font-size: 12px;
  text-align: center;
  color: #c3c3c3;
}
